.nav-container {
	width: 100%;
	min-height: 120px;
	display: flex;
	border-bottom: 2px solid #8C3503;
	justify-content: space-between;
	align-items: center;
	background-color: #1BDDF2;
	letter-spacing: 1px;
}

.nav-title {
	margin-left: 5%;
	font-size: 74px;
  color: #F2B807;
  font-family: 'Rubik Beastly', cursive;
  text-decoration: none;
  text-shadow: -3px 1px 0 #8C3503,
				  1px 1px 0 #8C3503,
				 /* 1px -1px 0 #8C3503; */
				-3px -1px 0 #8C3503;
}

.nav-title:hover {
  cursor: pointer;
	color: #888C11;
}

.navbar-links {
  margin-right: 5%;
  font-family: 'Piedra', cursive;
  font-size: 32px;
}


.navbar-links {
	display: flex;
	justify-content: space-evenly;
}

.nav-link {
	width: 120px;
	text-decoration: none;
	color: #888C11;
}

.nav-link:hover {
	cursor: pointer;
	color: #F2B807;
}

